const uni = require('uni').default;
























import pageHead from '../../../components/page-head.vue'

export default {
	data() {
		return {
			title: 'toast'
		}
	},
	methods: {
		toast1Tap: function () {
			uni.showToast({
				title: "默认"
			})
		},
		toast2Tap: function () {
			uni.showToast({
				title: "duration 3000",
				duration: 3000
			})
		},
		toast3Tap: function () {
			uni.showToast({
				title: "loading",
				icon: "loading",
				duration: 5000
			})
		},
		toast4Tap: function () {
			uni.showToast({
				title: "logo",
				image: "../../../static/uni@2x.png"
			})
		},
		hideToast: function () {
			uni.hideToast()
		}
	},
	components: {
		pageHead
	}
}



